import * as THREE from 'three'
import Experience from "./Experience.js"
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

export default class Camera {
    constructor() {
        this.experience = new Experience()
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.canvas = this.experience.canvas

        this.setInstance()
        this.setOrbitControls()
    }

    setInstance() {
        this.instance = new THREE.PerspectiveCamera(
            35,
            this.sizes.width / this.sizes.height,
            0.1,
            100
        )
        this.instance.position.set(27.556005248159888, 25.65242226195055, -27.929612918272298)


        this.scene.add(this.instance)
    }

    setOrbitControls() {
        this.controls = new OrbitControls(this.instance, this.canvas)
        this.controls.enableDamping = true
        this.controls.minPolarAngle = Math.PI / 4; // 30 grados en radianes
        this.controls.maxPolarAngle = Math.PI / 2;//Azimuth
        this.controls.minAzimuthAngle = Math.PI / 2; // 30 grados en radianes
        this.controls.maxAzimuthAngle = Math.PI;
        this.controls.minZoom = 10; // Distancia mínima de zoom
        this.controls.maxZoom = 10;
        this.controls.minDistance = 0; // Distancia mínima de zoom
        this.controls.maxDistance = 80;

    }

    resize() {
        this.instance.aspect = this.sizes.width / this.sizes.height
        this.instance.updateProjectionMatrix()
    }
    update() {
        this.controls.update()
    }
}