import * as THREE from 'three'
import Experience from "../Experience";
import gsap from 'gsap';

export default class Room {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.camera = this.experience.camera.instance
        this.sizes = this.experience.sizes
        this.resources = this.experience.resources
        this.time = this.experience.time
        this.debug = this.experience.debug
        this.raycaster = new THREE.Raycaster()

        // Debug
        if (this.debug.active) {
            this.debugFolder = this.debug.ui.addFolder('fox')
        }

        // Setup
        this.resource = this.resources.items.roomModel
        this.texture = this.resources.items.roomTexture
        this.texture.flipY = false
        this.texture.colorSpace = THREE.SRGBColorSpace

        this.setModel()

    }

    setModel() {
        this.model = this.resource.scene
        var material = new THREE.MeshBasicMaterial({
            map: this.texture // tu textura baked
        });
        this.model.traverse((child) => {
            if (child instanceof THREE.Mesh) {
                child.castShadow = true
                child.material = material
            }
        })

        this.scene.add(this.model)

    }

    setAnimations() {
        this.animation = {}
        this.animation.mixer = new THREE.AnimationMixer(this.model)
        this.animation.actions = {}

        this.animation.actions.idle = this.animation.mixer.clipAction(this.resource.animations[0])
        this.animation.actions.walking = this.animation.mixer.clipAction(this.resource.animations[1])
        this.animation.actions.running = this.animation.mixer.clipAction(this.resource.animations[2])

        this.animation.actions.current = this.animation.actions.idle
        this.animation.actions.current.play()

        this.animation.play = (name) => {
            const newAction = this.animation.actions[name]
            const oldAction = this.animation.actions.current

            newAction.reset()
            newAction.play()
            newAction.crossFadeFrom(oldAction, 1)

            this.animation.actions.current = newAction
        }

        // Debug
        if (this.debug.active) {
            const debugObject = {
                playIdle: () => { this.animation.play('idle') },
                playWalking: () => { this.animation.play('walking') },
                playRunning: () => { this.animation.play('running') }
            }
            this.debugFolder.add(debugObject, 'playIdle')
            this.debugFolder.add(debugObject, 'playWalking')
            this.debugFolder.add(debugObject, 'playRunning')

        }
    }
    transformControlsArticle() {
        // Raycaster
        this.raycaster = new THREE.Raycaster()

        // Mouse
        this.mouse = new THREE.Vector2()

        window.addEventListener('mousemove', (event) => {
            this.mouse.x = event.clientX / this.sizes.width * 2 - 1
            this.mouse.y = - (event.clientY / this.sizes.height) * 2 + 1

        })
        window.addEventListener('click', () => {
            console.log(this.intersects[0].object.position)

            let positionInit = this.intersects[0].object.position.y
            gsap.to(this.intersects[0].object.position, {
                y: 12,
                duration: .2,
                onComplete: () => {
                    gsap.to(this.intersects[0].object.position, {
                        y: positionInit
                    })
                }
            })

        })


    }

    update() {


    }
}