import * as THREE from 'three';
import Experience from "../Experience";
import Environment from './Environment';
import Room from './Room';
import Fox from './Fox';
import Github from './Github';
import Desktop from './Desktop';
import gsap from 'gsap';
const logo = document.getElementById('logo')

export default class World {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        console.log(logo)

        //BAckground
        this.overlay()


        // Wait for resources
        this.resources.on('ready', () => {
            // Setup
            this.room = new Room()
            this.github = new Github()
            this.desktop = new Desktop()

            gsap.to(this.overlayMaterial.uniforms.uAlpha, { value: 0, duration: 2 })
            gsap.to(logo, {
                opacity: 0, duration: 1, onComplete: () => {
                    logo.style.visibility = 'collapse'
                }
            })


            this.environment = new Environment()
        })


    }
    //overlay

    overlay() {
        const overlayGeometry = new THREE.PlaneGeometry(2, 2, 1, 1)
        this.overlayMaterial = new THREE.ShaderMaterial({
            transparent: true,
            uniforms: { uAlpha: { value: 1 } },
            vertexShader: `
                    void main()
                    {
                        gl_Position = vec4(position, 1.0);
                    }
                `,
            fragmentShader: `
                    uniform float uAlpha;
                    void main(){
                        gl_FragColor = vec4(1.0, 0.3497, 0.2704,uAlpha);
                    }
                `
        })
        const overlay = new THREE.Mesh(overlayGeometry, this.overlayMaterial)
        this.scene.add(overlay)

    }
    update() {

        if (this.github) {
            this.github.update()
        }
        if (this.desktop) {
            this.desktop.update()
        }
        if (this.room) {
            this.room.update()
        }

    }


}