import * as THREE from 'three'
import Experience from "../Experience";
import gsap from 'gsap';


export default class Desktop {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.camera = this.experience.camera
        this.sizes = this.experience.sizes
        this.resources = this.experience.resources
        this.time = this.experience.time
        this.debug = this.experience.debug
        this.transformControlsArticle()
        this.active = false

        this.targetObject = new THREE.Object3D();
        this.scene.add(this.targetObject);
        this.targetObject.position.set(0, 3, 0)
        this.camera.controls.target.copy(this.targetObject.position);

        console.log(this.resources)
        // Setup
        this.resource = this.resources.items.desktopModel
        this.texture = this.resources.items.screenTexture //screenHoverTexture
        this.texture.flipY = false
        this.texture.colorSpace = THREE.SRGBColorSpace

        this.textureHover = this.resources.items.screenHoverTexture //screenHoverTexture
        this.textureHover.flipY = false
        this.textureHover.colorSpace = THREE.SRGBColorSpace
        let audioSrc = window.location.hostname === 'localhost' ? '/audios/transition3.mp3' : '/isometric-room/audios/transition3.mp3';
        this.audio = new Audio(audioSrc)

        this.setModel()
        this.textureChange()
    }

    setModel() {
        this.model = this.resource.scene

        var material = new THREE.MeshBasicMaterial({
            map: this.texture // tu textura baked
        });

        this.model.traverse((child) => {
            if (child instanceof THREE.Mesh) {
                child.castShadow = true
                child.material = material
            }
        })

        this.scene.add(this.model)

    }
    textureChange() {
        window.addEventListener('mousemove', (event) => {
            this.model.traverse((child) => {
                if (this.intersects.length > 0) {
                    var material = new THREE.MeshBasicMaterial({
                        map: this.textureHover // tu textura baked
                    });
                    if (child instanceof THREE.Mesh) {
                        child.material = material
                    }
                } else {
                    var material = new THREE.MeshBasicMaterial({
                        map: this.texture // tu textura baked
                    });
                    if (child instanceof THREE.Mesh) {
                        child.material = material
                    }
                }

            })

        })
    }
    transformControlsArticle() {
        // Raycaster
        this.raycaster = new THREE.Raycaster()

        // Mouse
        this.mouse = new THREE.Vector2()

        window.addEventListener('mousemove', (event) => {
            this.mouse.x = event.clientX / this.sizes.width * 2 - 1
            this.mouse.y = - (event.clientY / this.sizes.height) * 2 + 1

        })
        window.addEventListener('mousedown', () => {
            this.mouseInit = this.mouse.clone()
            window.addEventListener('mouseup', () => {
                if (this.mouseInit.x === this.mouse.x) {
                    if (this.intersects.length > 0) {


                        gsap.to(this.targetObject.position, {
                            duration: 2,
                            x: this.model.children[0].position.x,
                            y: this.model.children[0].position.y,
                            z: this.model.children[0].position.z,
                            onStart: () => {

                                this.audio.currentTime = 0
                                this.audio.volume = 0.5
                                this.audio.play()
                            },
                            onUpdate: () => {

                                this.camera.controls.target.copy(this.targetObject.position);
                            }
                        });
                        gsap.to(this.camera.instance.position, {
                            x: 6.578146268992554,
                            y: 14.367765717783088,
                            z: -5.315578999999999,
                            duration: 2
                        });
                        this.active = true

                    } else {
                        if (this.active) {
                            this.audio.currentTime = 0
                            this.audio.volume = 0.5
                            this.audio.play()
                            gsap.fromTo(this.camera.instance.position, {
                                x: this.camera.instance.position.x,
                                y: this.camera.instance.position.y,
                                z: this.camera.instance.position.z
                            }, {
                                x: 27.556005248159888,
                                y: 25.65242226195055,
                                z: -27.929612918272298,
                                duration: 2
                            });
                            gsap.to(this.targetObject.position, {
                                duration: 2,
                                x: 0,
                                y: 3,
                                z: 0,
                                onUpdate: () => {
                                    // Actualizar la posición del objetivo de OrbitControls
                                    this.camera.controls.target.copy(this.targetObject.position);
                                }
                            });

                            this.active = false
                            this.camera.controls.enabled = true

                        }
                    }
                }

            })

        })



    }

    update() {
        //this.animation.mixer.update(this.time.delta * 0.001)
        this.raycaster.setFromCamera(this.mouse, this.camera.instance)
        this.intersects = this.raycaster.intersectObjects(this.model.children)

    }
}